  letter-spacing: 1px;
  font-size: 18px;
  margin: 5px 0;
}

.container {
  background-color: rgb(32, 43, 59);
  padding: 1rem;
  border-radius: 1rem;
  margin: 1rem auto;
}

@media only screen and (max-width:766px) {
  .App {
    padding: 1rem;
  }
}